.header {
  width: 100%;
  padding-top: 57px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  transition: padding-top .3s ease;
  padding-right: var(--scrollbar-current-width-y);
  @include media-up(993) {
    pointer-events: none;
  }
  @include media-down(1440) {
    padding-top: 45px;
  }
  @include media-down(1200) {
    padding-top: 35px;
  }
  @include media-down(992) {
    padding-top: 24px;
  }
  @include md {
    height: 80px;
  }
  @include xs {
    margin-top: 0;
  }

  &.scrolled {
    padding-top: 25px;
    @include media-down(992) {
      padding-top: 15px;
    }

    .nav__list {
      @include media-up(993) {
        background-color: #102250;
      }
    }
  }

  &__body {
    position: relative;
    display: flex;
    align-items: center;

    * {
      @include media-up(993) {
        pointer-events: all;
      }
    }

    @include media-down(992) {
      flex-direction: row-reverse;
      justify-content: space-between;
    }
  }

  &__container {
    max-width: 1677px;
    padding: 0 15px;
    margin: 0 auto;
    width: 100%;
    position: relative;
  }

  &__logo {
    display: block;
    max-width: 176px;
    width: 100%;
    margin-inline-end: 226px;
    @include media-down(1680) {
      max-width: 160px;
      margin-inline-end: 185px;
    }
    @include media-down(1440) {
      max-width: 145px;
      margin-inline-end: 125px;
    }
    @include media-down(1200) {
      max-width: 120px;
      margin-inline-end: 75px;
    }
    @include media-down(992) {
      max-width: 106px;
      margin: 0 auto;
      transform: translateX(-8px);
    }

    &__img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__logo-he {
    display: block;
    max-width: 95px;
    width: 100%;
    margin-inline-end: 422px;
    @include media-down(1680) {
      max-width: 80px;
      margin-inline-end: 350px;
    }
    @include media-down(1440) {
      max-width: 70px;
      margin-inline-end: 300px;
    }
    @include media-down(1200) {
      max-width: 60px;
      margin-inline-end: 250px;
    }
    @include media-down(992) {
      margin: 0 auto;
      transform: translateX(16px);
    }

    &__img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__mobile-burger {
    .icon-menu {
      display: none;
      @include md {
        display: block;
        position: relative;
        width: 16px;
        height: 16px;
        cursor: pointer;
        z-index: 2000;

        span,
        &::before,
        &::after {
          content: '';
          display: block;
          transition: all 300ms ease 0ms;
          right: 0;
          position: absolute;
          width: 16px;
          height: 2.5px;
          background-color: #fff;
        }
        &::before {
          top: 3px;
        }
        &::after {
          bottom: 3px;
        }
        span {
          top: calc(50% - 0.5px);
        }
        &._active {
          span {
            width: 0;
          }

          &::before {
            top: calc(50% - 0px);
            transform: rotate(-45deg);
            width: 100%;
          }

          &::after {
            bottom: calc(50% - 2px);
            transform: rotate(45deg);
            width: 100%;
          }
        }
      }
    }
  }
}
