.primary-btn {
  border-radius: 80px;
  background: rgba(255, 255, 255, 0.1);
  padding: 12px 20px;
  @include sm {
    font-size: 15px;
    line-height: 35px;
  }
  &:active {
    background-color: #fff;
    color: #102250;
    transition: all 0.3s;
  }
  &:hover {
    background: rgba(255, 255, 255, 0.25);
  }
}
