.contact-btn {
  color: #fff;
  font-size: 21px;
  padding: 10px 20px;
  line-height: 35px;
  border-radius: 80px;
  background: rgba(255, 255, 255, 0.1);
  font-weight: 400;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 10px;
  @include sm {
    font-size: 15px;
    line-height: 35px;
  }

  &:hover {
    background-color: #fff;
    color: #102250;
  }

  &:active {
    background-color: #fff;
    color: #102250;
    transition: all 0.3s;
  }

  .icon {
    display: block;
    margin-inline-end: 10px;

    &-email {
      width: 22px;
      min-width: 22px;
      height: 17px;
    }
    &-phone {
      width: 19px;
      min-width: 19px;
      height: 19px;
    }
  }
}
