.card-project {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 20px;

  &:hover {
    @include media-up(993) {
      &:before {
        opacity: 0;
      }

      .card-project {
        &__picture {
          &:before {
            opacity: 0;
          }

          &__img {
            filter: grayscale(0%);
          }
        }

        &__body-icon {
          opacity: 1;
          transform: translate(15%, 50%) scale(-1, 1);
        }

        &__popover {
          transform: translateY(0);
          opacity: 1;
          pointer-events: all;
        }
      }
    }
  }

  &:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 8;
    pointer-events: none;
    border-radius: inherit;
    transition: all .3s ease;
    border: 1px solid rgba(214, 225, 252, 0.50);
  }

  &__picture {
    width: 100%;
    height: auto;
    max-height: 100%;
    aspect-ratio: 1.65/1;
    position: relative;
    z-index: 1;
    border-radius: 20px;

    &:before {
      content: '';
      display: block;
      position: absolute;
      pointer-events: none;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 13;
      opacity: var(--cnfrnc-cover-opacity);
      background-color: rgba(1, 12, 39, 1);
      mix-blend-mode: var(--cnfrnc-cover-blend-mode);
      transition: all .3s ease;
      box-shadow: inset 0 -10px 20px 15px #010C27;
      @include media-down(992) {
        display: none;
      }
    }

    &__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      border-radius: 20px;
      transition: all .3s ease;
      @include media-up(993) {
        filter: grayscale(100%);
      }

      &.croped {
        z-index: 12;
        pointer-events: none;
        @include media-down(992) {
          display: none;
        }
      }
    }
  }

  &__picture-inner {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 5;
    overflow: hidden;

    &:after {
      content: '';
      display: block;
      position: absolute;
      pointer-events: none;
      width: calc(100% + 40px);
      height: calc(100% + 40px);
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      z-index: 12;
      @include media-down(992) {
        box-shadow: inset 0 -10px 20px 15px #010C27;
      }
    }
  }

  &__popover {
    position: absolute;
    z-index: 12;
    padding: 12px 51px 15px 24px;
    color: #010C27;
    text-align: start;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    border-radius: 8px;
    background: #fff;
    transform: translateY(40px);
    opacity: 0;
    pointer-events: none;
    transition: all .3s ease;
    @include media-down(1620) {
      font-size: 13px;
      line-height: 22px;
    }
    @include media-down(1440) {
      font-size: 12px;
      line-height: 18px;
    }
    @include media-down(1200) {
      display: none;
    }

    &.dafkesher {
      &.first {
        left: -5%;
        bottom: 10%;
        @include media-down(1440) {
          padding-right: 15px;
        }
      }

      &.second {
        padding-left: 35px;
        top: 18%;
        left: 53%;
        @include media-down(1440) {
          padding-left: 20px;
        }
      }
    }

    &.salary-surveys {
      &.first {
        padding-right: 10px;
        left: 24%;
        top: 28%;
      }

      &.second {
        padding: 12px 15px 15px 15px;
        top: 1%;
        right: -4%;
      }
    }
  }

  &__popover-head {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;
    color: #010C27;
    text-align: start;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    @include media-down(1440) {
      gap: 5px;
    }

    .bg-gradient {
      display: block;
      padding: 1px 5px 1px 6px;
      color: #FFF;
      text-align: start;
      border-radius: 5px;
      background: linear-gradient(180deg, #001B6C 0%, #003FFF 42.96%, #BE7BFA 100%);
    }
  }

  &__body {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    padding: 10px 52px 24px;
    position: relative;
    z-index: 2;
    box-shadow: 0 -10px 20px 15px #010C27;
    @include media-down(1440) {
      padding: 10px 40px 24px;
    }
    @include media-down(1200) {
      padding: 20px 25px 35px;
      box-shadow: 0 -5px 15px 10px #010C27;
    }
    @include media-down(992) {
      box-shadow: none;
      z-index: 20;
    }
  }

  &__title {
    display: block;
    margin-bottom: 14px;
    color: #FFF;
    font-size: 21px;
    font-style: normal;
    font-weight: 700;
    line-height: 1.6;
    @include media-down(992) {
      margin-bottom: 13px;
      font-size: 17px;
      line-height: 35px;
    }
  }

  &__content {
    flex: 1 0 auto;
    margin-bottom: 23px;
    color: #FFF;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.76;
    @include media-down(992) {
      margin-bottom: 13px;
      font-size: 15px;
      line-height: 27px;
    }
  }

  &__bottom-row {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 40px);
    margin-inline: -20px;
    row-gap: 40px;
    @include media-down(992) {
      flex-direction: column;
      row-gap: 5px;
    }
  }

  &__bottom-col {
    flex: 0 0 50%;
    padding-inline: 20px;
  }

  &__body-icon {
    width: 100%;
    position: absolute;
    bottom: 0;
    right: 0;
    pointer-events: none;
    transition: all .3s ease;
    transform: translate(15%, 50%) scale(-1, 1);
    @include media-up(993) {
      opacity: 0;
      transform: translate(15%, calc(50% + 40px)) scale(-1, 1);
    }

    &.blobs {
      max-width: 36%;
      @include media-down(768) {
        max-width: 170px;
      }
    }

    &.chart {
      max-width: 37%;
      @include media-down(768) {
        max-width: 175px;
      }
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__link {
    font-size: 17px;
    line-height: 30px;
    white-space: initial;
    text-align: start;
    @include media-down(992) {
      font-size: 15px;
      line-height: 26px;
    }

    &,
    &:after {
      border: 0;
      text-decoration: underline;
    }

    &:after {
      width: 100%;
    }
  }
}
