/**
 * Functions for ViewPort Calculating
============
  SCSS
  .selector {
    font-size: px(18);
    @include md
      font-size: pxm(14);
    @include xs
      font-size: pxs(12);
  }

  CSS RESULT
  .selector {
    font-size: 18px;
  }
  @media (max-width: 768px) {
    .selector {
      font-size: 14px;
    }
  }
  @media (max-width: 320px) {
    .selector {
      font-size: 12px;
    }
  }
============
*/
/**
  function VA => use for calculate letter-spacing from photoshop
============
  SCSS
  .selector {
    letter-spacing: VA(120, 14);
  }

  CSS RESULT
  .selector {
    letter-spacing: 1.68px;
  }
============
  $unit => VA value in photoshop
  $font_size => how px font-size in photoshop ||| default 16
  $return px | em ||| default px
 */
:root {
  --baseWhite: #fff;
  --baseBlack: #000;
  --baseText: #000;
  --text-gradient: linear-gradient(360deg, rgba(0,27,108,1) 0%, rgba(0,63,255,1) 15%, rgba(190,123,250,1) 80%);
  --header-height: 77px;
  --scrollbar-current-width-y: 0px;
  --scrollbar-static-width-y: 0px;
  --cnfrnc-cover-blend-mode: color;
  --cnfrnc-cover-opacity: 1;
}

html.scroll-locked,
body.scroll-locked {
