}
.floating-button svg g path {
  animation: myAnimation 7.5s infinite linear reverse;
  transform-origin: 50% 50%;
}
@media (min-width: 991px) {
  .floating-button svg circle {
    transition: all 0.3s;
    fill: unset;
    stroke: unset;
  }
  .floating-button svg mask {
    transition: all 0.3s;
    fill: #fff;
  }
  .floating-button svg g path {
    transition: all 0.3s;
    animation: myAnimation 7.5s infinite linear reverse;
    transform-origin: 50% 50%;
    fill: #fff;
  }
}
@media screen and (min-width: 993px) {
  .floating-button svg:hover circle {
    fill: #fff !important;
    stroke: gray !important;
  }
  .floating-button svg:hover mask {
