.nav {
  transition: all 0.5s;

  @include xl {
    position: static;
    transform: unset;
  }
  @include sm {
    position: static;
    transform: unset;
  }
  @include md {
    overflow: hidden;
    opacity: 0;
    position: fixed;
    left: 100%;
    width: 100%;
    height: 100%;
    max-width: unset;
    top: 0;
    transition: all 0.75s;
    background: linear-gradient(
        62deg,
        #be7bfa 0%,
        rgba(0, 63, 255, 0.57) 23.99%,
        rgba(1, 12, 39, 0) 66.22%,
        #010c27 100%
    ),
    #010c27;
    z-index: 1000;

    &._active {
      overflow: visible;
      opacity: 1;
      left: 0;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.15);
    padding: 10px 32px;
    border-radius: 80px;
    gap: 4px;
    transition: background-color .3s ease;
    @include md {
      gap: 0;
      flex-direction: column;
      align-items: center;
      height: 100%;
      background-color: unset;
      justify-content: center;
    }
  }

  &__item {
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 80px;
    transition: all 0.3s;
    @include media-down(1440) {
      padding: 10px 15px;
    }
    @include media-down(1200) {
      padding: 5px 10px;
    }
    @include media-down(992) {
      width: fit-content;
      margin: 0 auto;
    }

    &:hover {
      background: rgba(255, 255, 255, 1);
      color: #102250;
    }

    &:active {
      background-color: #fff;
      color: #102250;
    }
  }

  &__link {
    font-size: 21px;
    line-height: 35px;
    text-transform: uppercase;
    font-weight: 400;
    @include media-down(1440) {
      font-size: 18px;
      line-height: 28px;
    }
    @include media-down(1200) {
      line-height: 24px;
    }
    @include md {
      display: inline-block;
      width: 100%;
      text-align: center;
      font-size: 23px;
    }
  }
}
