.direction-rtl,
[lang='he'],
[dir='rtl'] {
  .floating-button {
    svg {
      g {
        path {
          animation: myAnimation 7.5s infinite linear;
        }
      }
      @media (min-width: 991px) {
        g {
          path {
            animation: myAnimation 7.5s infinite linear;
          }
        }
      }
    }
  }

  .hero {
    .hero__title {
      margin-bottom: 210px;
      @include media-up(1440) {
        font-size: 106px;
        line-height: 140px;
      }
      @include media-down(768) {
        margin-bottom: 275px;
        padding-bottom: 30px;
      }

      @include media-down(1440) {
        br {
          display: none;
        }
      }
    }

    .hero__videos {
      transform: scale(-1, 1) translateX(50%);
    }
  }

  .conferences-group {
    .conferences {
      @include media-down(768) {
        padding-top: 120px;
      }
      &:nth-child(even) {
        .conferences__img-container {
          justify-content: flex-end;
          transform: scale(-1, 1);
        }

        .conferences__info-text {
          right: 23px;
          left: unset;
          transform-origin: right top;
          transform: rotate(-270deg) translateX(50%);
          @include media-down(992) {
            right: 5px;
            transform: rotate(-270deg) translateX(150%);
          }
        }
      }

      .conferences__info-text {
        right: unset;
        left: 23px;
        transform-origin: left top;
        transform: rotate(270deg) translateX(-50%);
        @include media-down(992) {
          left: 5px;
          transform: rotate(270deg) translateX(-150%);
        }
      }

      .conferences__text {
        max-width: 400px;
      }

      .conferences__img-container {
        justify-content: flex-start;
        transform: scale(-1, 1);
      }
    }
  }

  .card-project {
    &:hover {
      @include media-up(993) {
        .card-project__body-icon {
          transform: translate(-15%, 50%) scale(1, 1);
        }
      }
    }

    .card-project__content {
      max-width: 463px;
    }

    .card-project__bottom-row {
      width: calc(100% + 25px);
      margin-inline: -25px;
    }

    .card-project__bottom-col {
      padding-inline: 25px;
      flex: 0 0 fit-content;
      width: fit-content;
    }

    .card-project__body-icon {
      bottom: 0;
      right: unset;
      left: 0;
      transform: translate(-15%, calc(50% + 40px)) scale(1, 1);
      @include media-down(992) {
        transform: translate(-13%, 48%) scale(1, 1);

        &.chart {
          transform: translate(-20%, 27%) scale(1, 1);
        }
      }
    }
  }

  .about__video-container {
    justify-content: flex-end;
  }

  .footer__copyright {
    @include media-down(992) {
      max-width: 152px;
    }
  }

  .modal-heart__image {
    right: unset;
    left: 50px;
    bottom: -46px;
  }

  .modal-heart-small__image {
    right: unset;
    left: -34px;
    bottom: 70px;
  }

  .modal__error-text {
    max-width: 481px;
  }
}
