.main-title {
  text-align: center;
}

.section-title {
  color: #fff;
  font-size: 50px;
  font-weight: 700;
  line-height: 1.01;
  letter-spacing: 1px;
  @include media-down(1440) {
    font-size: 44px;
  }
  @include media-down(1200) {
    font-size: 36px;
  }
  @include media-down(768) {
    font-size: 32px;
  }
}

