html,
body {
  &.scroll-locked {
    overflow: hidden;
  }
}

body {
  font-family: 'Anomalia', sans-serif;
  font-weight: 700;
  line-height: 1.3;
  font-size: px(21);
  color: var(--baseWhite);
  background-color: #010C27;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  padding-right: var(--scrollbar-current-width-y);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

button {
  padding: 0;
  background-color: initial;
  border: none;
  cursor: pointer;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

textarea {
  resize: none;
}

section {
  position: relative;
}

input,
select,
button,
textarea {
  font: inherit;
  color: inherit;
  outline: none !important;
}

