fieldset {
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-block-start: 0;
  padding-inline-start: 0;
  padding-inline-end: 0;
  padding-block-end: 0;
  min-inline-size: min-content;
  border-width: 0;
  border-style: none;
  border-color: transparent;
  border-image: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

.wrapper {
  min-height: 100vh;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  position: relative;
}

.content {
  flex: 1 0 auto;
  overflow: hidden;
  position: relative;
  z-index: 500;
}

.container {
  width: 100%;
  max-width: px(1714);
  margin: 0 auto;
  padding-right: px(32);
  padding-left: px(32);
  @include media-down(768) {
    padding-right: px(16);
    padding-left: px(16);
  }
}

.container-fluid {
  padding-right: px(15);
  padding-left: px(15);
  @include media-breakpoint-down(xs) {
    padding-left: pxs(15);
    padding-right: pxs(15);
  }
}

.row {
  display: flex;
  width: 100% + px(30);
  margin-inline: px(-15);
}

.col {
  padding-inline: px(15);
}

.important_none {
  display: none !important;
}

.custom-scrollbar {
  &::-webkit-scrollbar {
    width: px(4);
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: #00000081;
  }
}
