.conferences-group {
  .conferences {
    &:nth-child(even) {
      .conferences__info-text {
        right: unset;
        left: 29px;
        transform-origin: left bottom;
        transform: rotate(90deg) translateX(-50%);
        @include media-down(992) {
          transform: rotate(90deg) translateX(100%);
          left: 5px;
        }
      }

      .conferences__img-container {
        &:after {
          left: unset;
          right: 0;
          background: linear-gradient(270deg, rgba(1, 12, 39, 1) 45%, rgba(1, 12, 39, .75) 70.48%, rgba(1, 12, 39, 0) 100%);
        }
      }
    }

    &:nth-child(3) {
      .conferences__img-container__img {
        transform: scale(-1, 1);
      }
    }
  }

  .conferences {
    display: flex;
    align-items: center;
    height: 100vh;
    min-height: fit-content;
    max-height: 1000px;
    padding-top: 100px;
    padding-bottom: 100px;
    position: relative;
    overflow: hidden;
    @include media-down(992) {
      height: auto;
      max-height: initial;
      padding-top: 134px;
      padding-bottom: 500px;
    }
    @include media-down(576) {
      padding-bottom: 304px;
    }

    &.second {
      @include media-down(992) {
        .conferences__img-container__img {
          object-position: 85%;
        }
      }

      .conferences__img-container {
        justify-content: flex-start;

        &:after {
          width: 60%;
        }

        &__image {
          width: 70%;
        }
      }

      .conferences__body {
        margin-inline-start: auto;
        margin-inline-end: 0;
      }
    }

    &.third {
      @include media-down(992) {
        .conferences__img-container__img {
          object-position: 50%;
        }
      }
    }

    &:hover {
      .container {
        transform: translateY(0);
      }

      .conferences__img-container {
        &:before {
          opacity: 0;
        }

        &__img {
          filter: grayscale(0%);
        }
      }

      .conferences__link {
        opacity: 1;
      }
    }

    .container {
      position: relative;
      z-index: 20;
    }

    &__link {
      margin-top: 23px;
      @include media-up(993) {
        opacity: 0;
      }
      @include media-down(992) {
        margin: 23px auto 0;
      }
    }

    &__img-container {
      display: flex;
      justify-content: flex-end;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      @include media-down(992) {
        flex-direction: column;
        justify-content: flex-end !important;
      }

      &:before,
      &:after {
        content: '';
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transition: all 0.3s ease;
        pointer-events: none;

      }

      &:before {
        width: 100%;
        z-index: 2;
        opacity: var(--cnfrnc-cover-opacity);
        background-color: rgba(1, 12, 39, 1);
        mix-blend-mode: var(--cnfrnc-cover-blend-mode);
        @include media-down(992) {
          opacity: 0;
        }
      }

      &:after {
        width: 52%;
        z-index: 3;
        background: linear-gradient(90deg, rgba(1, 12, 39, 1) 45%, rgba(1, 12, 39, .75) 70.48%, rgba(1, 12, 39, 0) 100%);
        @include media-down(992) {
          width: 100% !important;
          height: 150% !important;
          top: unset;
          bottom: 0;
          opacity: 1;
          background: linear-gradient(0deg, rgba(1, 9, 28, 0) 15%, rgba(1, 12, 39, 1) 35%) !important;
        }
        @include media-down(576) {
         height: 125% !important;
        }
      }

      &__image {
        width: 76%;
        height: 100%;
        position: relative;
        z-index: 1;
        @include media-down(992) {
          width: 100% !important;
          height: 500px !important;
        }
        @include media-down(576) {
          height: 304px !important;
        }
      }

      &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: grayscale(100%);
        transition: all .3s ease;
        @include media-down(992) {
          filter: grayscale(0);
          object-position: 75% 0;
        }
      }
    }

    &__info-text {
      position: absolute;
      top: 50%;
      right: 29px;
      color: #fff;
      z-index: 5;
      text-align: center;
      font-size: 17px;
      line-height: 30px;
      transform-origin: right bottom;
      transform: rotate(-90deg) translateX(50%);
      font-weight: 400;
      @include media-down(992) {
        color: rgba(255, 255, 255, 0.50);
        font-size: 8px;
        line-height: 1;
        letter-spacing: 0.16px;
        transform: rotate(-90deg) translateX(-75%);
        right: 5px;
      }
    }

    &__body {
      display: flex;
      width: fit-content;
      margin-inline-end: auto;
      flex-direction: column;
      gap: 23px;
      @include media-down(992) {
        margin-inline-end: auto !important;
        margin-inline-start: auto !important;
      }
      @include media-down(768) {
        gap: 18px;
      }
    }

    &__top {
      display: flex;
      align-items: center;
      position: relative;
      z-index: 20;
      @include media-down(992) {
        justify-content: center;
      }
    }

    &__img-content {
      max-width: 107px;
      width: 100%;
      height: fit-content;
      border-radius: 18%;
      overflow: hidden;
      margin-inline-end: 30px;
      @include media-down(1440) {
        max-width: 90px;
      }
      @include media-down(1200) {
        max-width: 75px;
        margin-inline-end: 20px;
      }
      @include media-down(768) {
        max-width: 60px;
        margin-inline-end: 10px;
      }

      &__img {
        width: 100%;
        height: 100%;
        aspect-ratio: 1/1;
        object-fit: contain;
      }
    }

    &__title {
      color: #fff;
      font-size: 50px;
      font-weight: 700;
      line-height: 1.01;
      letter-spacing: 1px;
      @include media-down(1440) {
        font-size: 44px;
      }
      @include media-down(1200) {
        font-size: 36px;
      }
      @include media-down(768) {
        font-size: 32px;
      }
    }

    &__subtitle {
      color: #fff;
      font-size: 35px;
      font-weight: 400;
      line-height: 1.48;
      letter-spacing: 1px;
      @include media-down(1440) {
        font-size: 30px;
      }
      @include media-down(1200) {
        font-size: 25px;
      }
      @include media-down(992) {
        display: none;
      }
    }

    &__bottom {
      position: relative;
      z-index: 20;
    }

    &__text {
      color: #fff;
      font-size: 21px;
      line-height: 1.6;
      max-width: 550px;
      font-weight: 400;
      @include media-down(1200) {
        font-size: 18px;
        max-width: 470px;
      }
      @include media-down(992) {
        text-align: center;
      }
      @include media-down(768) {
        font-size: 15px;
      }
    }
  }
}
