.primary-link {
  display: block;
  width: fit-content;
  position: relative;
  font-family: 'Anomalia', sans-serif;
  color: $baseWhite;
  text-align: right;
  font-size: 21px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  text-transform: uppercase;
  white-space: nowrap;
  border-bottom: 1px solid $baseWhite;
  transition: all .3s ease;

  &:hover {
    @include media-up(1025) {
      color: transparent;
      border-color: transparent;

      &:after {
        opacity: 1;
      }
    }
  }

  &:after {
    content: attr(data-holder);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font: inherit;
    pointer-events: none;
    transition: all .3s ease;
    opacity: 0;
    background: var(--text-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
    border-bottom: 1px solid rgba(0, 63, 255, 1);
  }
}
