.projects {
  padding-top: 100px;
  padding-bottom: 75px;
  @include media-down(992) {
    padding: 80px 0 40px;
  }

  &__container {
    max-width: 1683px;
  }

  &__title {
    margin-bottom: 84px;
    @include media-down(1440) {
      margin-bottom: 77px;
    }
    @include media-down(1200) {
      margin-bottom: 63px;
    }
    @include media-down(768) {
      margin-bottom: 50px;
      text-align: center;
      font-size: 27px;
      line-height: normal;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 140px);
    margin-inline: -70px;
    @include media-down(1440) {
      width: calc(100% + 80px);
      margin-inline: -40px;
    }
    @include media-down(1200) {
      width: calc(100% + 40px);
      margin-inline: -20px;
    }
    @include media-down(768) {
      flex-direction: column-reverse;
      row-gap: 70px;
    }
  }

  &__col {
    flex: 0 0 50%;
    width: 50%;
    padding-inline: 70px;
    @include media-down(1440) {
      padding-inline: 40px;
    }
    @include media-down(1200) {
      padding-inline: 20px;
    }
    @include media-down(768) {
      flex: 0 0 100%;
      width: 100%;
    }
  }

  &__cards {
    margin-top: 84px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include lg {
      flex-direction: column;
      gap: 70px;
    }

    @include xs {
      margin-top: 50px;
    }
  }

  &__card {
    position: relative;
    width: 756px;
    height: 704px;
    border-radius: 10px;
    background: #010c27;
    border: 1px solid;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
    cursor: pointer;
    border-radius: 20px;
    @include xl {
      width: 650px;
    }
    @include md {
      width: 100%;
    }
    @include sm {
      height: auto;
    }
    &:hover {
      .card-shedule__image,
      .card-floating__image {
        opacity: 1;
      }
      .projects__img {
        filter: grayscale(0%);
      }
      .projects__image {
        &::after {
          opacity: 0;
        }
      }
      .projects__img-container {
        &::after {
          opacity: 0;
        }
      }
    }
  }
  &__img-container {
    height: 455px;
    overflow: hidden;
    border-radius: 20px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 1;
      transition: 0.3s;
      background: linear-gradient(
        360deg,
        #010c27 0%,
        rgba(1, 12, 39, 0.6) 24.37%,
        rgba(1, 12, 39, 0.12) 61.25%,
        rgba(1, 12, 39, 0) 100%
      );
    }

    @include sm {
      height: auto;
    }
  }

  &__image {
    &:after {
      background: #102250;
      mix-blend-mode: color;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      content: '';
      z-index: 2;
      opacity: 0.5;
    }
    .projects__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: relative;
      transition: all 0.3s;
      filter: grayscale(100%);
      @include sm {
        height: unset;
        object-fit: contain;
      }
    }
  }

  &__text {
    padding: 0 52px;
    position: relative;
    z-index: 100;
    @include xl {
      padding: 20px;
    }
  }

  &__text-links {
    display: flex;
    justify-content: space-between;
    padding-right: 141px;
    @include md {
      flex-direction: column;
      gap: 5px;
    }
  }

  &__link-block {
    display: flex;
    flex-direction: column;
    max-width: 192px;
  }

  &__text-title {
    margin-bottom: 14px;
    color: #fff;
    font-size: 21px;
    font-weight: 700;
    line-height: 35px;
    @include xs {
      font-size: 17px;
      margin-bottom: 0px;
    }
  }

  &__text-subtitle {
    margin-bottom: 10px;
    color: #fff;
    font-size: 17px;
    line-height: 30px;
    max-width: 651px;
    font-weight: 400;
    @include xs {
      font-size: 15px;
    }
  }

  &__text-link {
    color: #fff;
    font-size: 17px;
    line-height: 30px;
    font-weight: 400;
    text-decoration-line: underline;
    @include xs {
      font-size: 15px;
    }
  }
}

.card-shedule__image,
.card-floating__image {
  position: absolute;
  bottom: -45px;
  z-index: 100;
  right: 0;
  transition: opacity 0.3s;
  opacity: 0;
  @include md {
    bottom: 0px;
  }
  @include xs {
    bottom: -40px;
  }
}

.card-shedule__image,
.card-floating__image {
  .card-floating__img,
  .card-shedule__img {
    @include xs {
      width: 177px;
    }
  }
}
