.about {
  padding: 75px 0 20px;
  @include media-down(992) {
    padding: 40px 0;
  }

  &__title {
    text-align: center;
    margin-bottom: 50px;
    @include media-down(1200) {
      margin-bottom: 40px;
    }
    @include media-down(768) {
      margin-bottom: 34px;
    }
  }

  &__text {
    color: #fff;
    text-align: center;
    font-size: 50px;
    letter-spacing: 1px;
    font-weight: 400;
    line-height: normal;
    max-width: 1372px;
    margin: 0 auto;
    position: relative;
    z-index: 5;
    @include media-down(1440) {
      font-size: 40px;
      max-width: 1200px;
    }
    @include media-down(1200) {
      font-size: 30px;
      max-width: 992px;
    }
    @include media-down(992) {
      margin-bottom: 17px;
    }
    @include media-down(768) {
      font-size: 23px;
    }
  }

  &__text-span {
    background: linear-gradient(360deg, rgba(0,27,108,1) 0%, rgba(0,63,255,1) 15%, rgba(190,123,250,1) 60%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
  }

  &__video-container {
    display: flex;
    justify-content: flex-start;
    position: relative;
    z-index: 1;
    width: 100%;
    margin-inline-end: auto;
    animation: levitation 10s ease infinite;
    @include media-up(993) {
      margin-top: -60px;
    }

    &:before {
      content: '';
      display: block;
      width: 100%;
      max-width: 75%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 3;
      box-shadow: inset -200px 0 100px 100px #010C27;
      @include media-down(1440) {
        box-shadow: inset -150px 0 75px 75px #010C27;
      }
      @include media-down(1200) {
        box-shadow: inset -100px 0 50px 50px #010C27;
      }
      @include media-down(992) {
        max-width: initial;
        width: calc(100% + 50px);
        margin-inline: -25px;
        left: 50%;
        transform: translateX(-50%);
        box-shadow: inset 0 0 20px 20px #010C27;
      }
    }
  }

  &__video {
    width: 100%;
    max-width: 70%;
    height: 100%;
    object-fit: contain;
    position: relative;
    z-index: 1;
    @include media-down(992) {
      max-width: 100%;
    }
  }
}
