.footer {
  position: relative;
  z-index: 250;
  padding: px(23) 0;
  background-color: #fff;
  color: var(--baseWhite);
  display: flex;
  align-items: center;
  @include media-down(768) {
    padding: px(16) px(24);
  }

  &__wrap {
    display: flex;
    align-items: center;
    @include media-down(992) {
      justify-content: space-between;
    }
  }

  &__logo-link {
    display: block;
    max-width: 176px;
    width: 100%;
    margin-inline-start: -176px;
    transform: translateX(100%);
    @include media-down(1680) {
      max-width: 160px;
      margin-inline-start: -160px;
    }
    @include media-down(1440) {
      max-width: 145px;
      margin-inline-start: -145px;
    }
    @include media-down(1200) {
      max-width: 120px;
      margin-inline-start: -120px;
    }
    @include media-down(992) {
      max-width: 106px;
      margin-inline-start: 0;
      transform: translateX(0);
    }

    &__img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__logo-link-he {
    display: block;
    max-width: 95px;
    width: 100%;
    margin-right: -95px;
    transform: translateX(-100%);
    @include media-down(1680) {
      max-width: 80px;
      margin-right: -80px;
    }
    @include media-down(1440) {
      max-width: 70px;
      margin-right: -70px;
    }
    @include media-down(1200) {
      max-width: 60px;
      margin-right: -60px;
    }
    @include media-down(992) {
      margin: 0;
      transform: translateX(0);
    }

    &__img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__copyright {
    color: #010c27;
    font-size: 17px;
    line-height: 30px;
    font-weight: 400;
    margin: 0 auto;
    text-align: center;
    @include media-down(992) {
      margin: 0;
      max-width: 185px;
      text-align: end;
      font-size: 11px;
      line-height: 16px;
    }
  }
}
.logo__image {
  img {
    @include xs {
      width: 100px;
    }
  }
}
