.modal {
  padding-block: 10vh 5vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba($baseBlack, 0.5);
  z-index: 2000;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all .5s ease, padding-right 0s;
  padding-right: var(--scrollbar-current-width-y);
  @include media-down(1200) {
    padding-block: 5vh;
  }
  @include media-down(768) {
    padding-block: 20px;
    background: #010c27;
  }

  &.show {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;

    .modal__dialog {
      top: 0;
    }
  }

  &__dialog {
    width: calc(100% - 64px);
    margin: 0 auto;
    position: relative;
    background: #010c27;
    max-width: 1364px;
    border-radius: 20px;
    transition: all .5s ease;
    @include media-down(768) {
      width: calc(100% - 32px);
    }

    &::after {
      content: '';
      position: absolute;
      top: -3px;
      left: -3px;
      right: -3px;
      bottom: -3px;
      border-radius: 23px;
      border: 1px solid transparent;
      background: linear-gradient(0deg, rgba(0, 27, 108, 1), rgba(0, 63, 255, 1), rgba(190, 123, 250, 1)) 0 0 / cover no-repeat;
      z-index: -1;
    }

    @include xl {
      max-width: 1300px;
    }
    @include lg {
      max-width: 900px;
    }
    @include md {
      max-width: 700px;
    }
    @include sm {
      max-width: unset;
      border-radius: unset;
      &::after {
        content: unset !important;
      }
    }
    @include xs {
      background: unset;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: px(30) px(20) px(10);
  }

  &__header-close-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: px(30);
    min-width: px(30);
    height: px(30);
  }

  &__card {
    padding: 62px;
    @include md {
      padding: 30px;
    }
  }

  &__card-title {
    color: #fff;
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 1px;
    @include md {
      font-size: 30px;
    }
    @include sm {
      font-size: 25px;
    }
    @include xs {
      text-align: center;
      font-size: 20px;
    }
  }

  &__title-span {
    display: block;
    font-size: 20px;
    background-image: var(--text-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
    @include sm {
      font-size: 15px;
    }
  }

  &__form {
    width: 100%;
    display: flex;
    border-radius: 80px;
    border: 1px solid rgba(214, 225, 252, 0.15);
    background: rgba(1, 12, 39, 0.75);
    padding: 10px 25px;
    margin-top: 46px;
    justify-content: space-between;
    min-height: 105px;
    transition: border 0.3s;

    &.error {
      border: 1px solid #ff3864;
    }

    @include md {
      flex-direction: column;
      border-radius: 20px;
      gap: 20px;
      padding: 20px 25px;
    }
    @include xs {
      margin-top: 20px;
    }
  }

  &__form-wrapper {
    flex-basis: 215px;
    padding: 10px 22px;
    position: relative;

    &:not(:last-of-type) {
      &::after {
        content: '';
        height: 90%;
        width: 1px;
        background: rgba(214, 225, 252, 0.15);
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &--question {
      flex: 1 1 auto;

      &::after {
        content: unset;
      }
    }

    @include md {
      flex-basis: 100px;
      border-bottom: 1px solid;
      &::after {
        content: unset !important;
      }
    }
    @include sm {
      flex-basis: 50px;
    }
  }

  &__label {
    color: rgba(214, 225, 252, 0.5);
    font-size: 17px;
    font-weight: 400;
    line-height: 30px;
    @include lg {
      font-size: 18px;
    }
  }

  &__input {
    font-family: Aran;
    background: unset;
    width: 100%;
    border: unset;
    color: #fff;
    font-size: 19px;
    font-family: Aran;
    line-height: 28px;
    font-weight: 100;

    &::placeholder {
      font-weight: 400;
      color: rgba(214, 225, 252, 0.5);
      font-size: 16px;
      font-family: Anomalia;
      line-height: 30px;
    }

    &--textarea {
      width: 100%;
      resize: none;
      overflow: hidden;
    }

    &.error {
      color: rgb(255, 56, 100);
    }
  }

  &__form-submit {
    color: #fff;
    text-align: center;
    font-size: 21px;
    line-height: 35px;
    padding: 10px 40px;
    border-radius: 80px;
    background: rgba(255, 255, 255, 0.1);
    opacity: 0.5;
    font-weight: 400;
    display: flex;
    margin-top: auto;
    margin-bottom: auto;

    &:hover {
      color: #102250;
      background: #fff;
      opacity: 1;
    }

    @include md {
      justify-content: center;
    }
  }

  &__contacts-buttons {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 30px;
    @include md {
      justify-content: center;
      flex-direction: column;
    }
  }

  &__contacts-title {
    font-size: 21px;
    color: #fff;
    font-size: 21px;
    font-weight: 700;
    line-height: 35px;
    margin-top: 46px;
    @include md {
      text-align: center;
    }
  }

  &__error {
    max-width: 656px;
    margin-bottom: 46px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    opacity: 0;
    height: 0;
    overflow: hidden;
    visibility: hidden;
    margin-top: 0;
    transition: all 0.5s;

    &.active {
      margin-top: 47px;
      opacity: 1;
      height: 100%;
      width: 100%;
      overflow: visible;
      visibility: visible;
    }

    @include md {
      margin: 0 auto;
      padding-top: 30px;
    }
  }

  &__error-text {
    color: #ff3864;
    font-size: 20px;
    font-family: Aran;
    line-height: 30px;
    font-weight: 400;
    @include md {
      text-align: center;
    }
  }

  &__after-send {
    display: flex;
    flex-direction: column;
    padding: 20px 40px;
    @include sm {
      padding: unset;
    }
  }

  &__after-send-title {
    color: #fff;
    font-size: 21px;
    line-height: 35px;
  }

  &__after-send-text {
    color: #fff;
    font-size: 17px;
    line-height: 30px;
    font-weight: 400;
  }
}

.close-modal {
  position: absolute;
  right: 32px;
  top: 24px;
  background: linear-gradient(rgba(0, 27, 108, 1), rgba(0, 63, 255, 1), rgba(190, 123, 250, 1));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  @include sm {
    right: 23px;
    top: 11px;
  }
}

.svg-close-dims {
  width: 26px;
  height: 26px;
  fill: rgb(0, 27, 108);
  @include sm {
    width: 20px;
    height: 20px;
  }
}

.modal-heart-small__image {
  position: absolute;
  right: -34px;
  bottom: 70px;
  @include sm {
    display: none;
  }
}

.modal-heart__image {
  position: absolute;
  right: 50px;
  bottom: -46px;
  @include sm {
    display: none;
  }
}
