.contact {
  padding-top: 25px;
  padding-bottom: 76px;
  @include media-down(992) {
    padding-top: 40px;
    padding-bottom: 80px;
  }

  &__title {
    text-align: center;
    margin-bottom: 50px;
    @include media-down(1200) {
      margin-bottom: 40px;
    }
    @include media-down(768) {
      margin-bottom: 25px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: center;
    @include xl {
      flex-direction: column-reverse;
    }

    .contact-btn {
      @include media-down(768) {
        font-size: 15px;
        line-height: 35px;

        .icon {
          &-phone {
            width: 19px;
            min-width: 19px;
            height: 19px;
          }
          &-email {
            width: 20px;
            min-width: 20px;
            height: 20px;
          }
        }
      }
    }
  }
}
