.floating-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
  z-index: 999;
  background-color: unset;

  @keyframes myAnimation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  svg {
    fill: rgba(255, 255, 255, 0.1);
    @include xs {
      width: 100px;
      height: 100px;
    }
    g {
      path {
        animation: myAnimation 7.5s infinite linear reverse;
        transform-origin: 50% 50%;
      }
    }
    @media (min-width: 991px) {
      circle {
        transition: all 0.3s;
        fill: unset;
        stroke: unset;
      }
      mask {
        transition: all 0.3s;
        fill: #fff;
      }
      g {
        path {
          transition: all 0.3s;
          animation: myAnimation 7.5s infinite linear reverse;
          transform-origin: 50% 50%;
          fill: #fff;
        }
      }
    }
    &:hover {
      @include media-up(993) {
        circle {
          fill: #fff !important;
          stroke: gray !important;
        }
        mask {
          fill: #102250;
        }
        path {
          fill: #102250;
        }
      }
    }
    @include media-down(992) {
      circle {
        fill: #fff !important;
      }
      mask {
        fill: #102250;
      }
      path {
        fill: #102250;
      }
    }
  }
}

