.secondary-btn {
  border-radius: 80px;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  @include sm {
    font-size: 15px;
    line-height: 35px;
  }
  &:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #fff;
  }
  &:active {
    background-color: #fff;
    color: #102250;
    transition: all 0.3s;
  }
}
