.hero {
  padding: 277px 0 380px;
  position: relative;
  @include media-down(1440) {
    padding: 220px 0 300px;
  }
  @include media-down(1200) {
    padding: 185px 0 300px;
  }
  @include media-down(992) {
    padding: 210px 0 140px;
  }
  @include media-down(768) {
    padding: 190px 0 68px;
  }

  .container {
    position: relative;
    z-index: 2;
  }

  // .hero__body

  &__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  // .hero__title

  &__title {
    margin-bottom: 136px;
    color: #fff;
    text-align: center;
    font-size: 87px;
    line-height: 1.3;
    letter-spacing: 1.74px;
    max-width: 1380px;
    font-weight: 400;
    opacity: 0;
    @include media-down(1440) {
      max-width: 1180px;
      margin-bottom: 115px;
      font-size: 70px;
    }
    @include media-down(1200) {
      max-width: 980px;
      margin-bottom: 85px;
      font-size: 60px;
    }
    @include media-down(992) {
      max-width: 875px;
      font-size: 50px;
    }
    @include media-down(768) {
      margin-bottom: px(122);
      font-size: 32px;
      letter-spacing: 0.64px;
      line-height: 1.5;

      .gradient-text {
        display: block;
      }
    }
  }

  &__title-span {
    background: var(--text-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
  }

  &__buttons {
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    gap: 6px;
    justify-content: center;
    position: relative;
    opacity: 0;
    @include media-down(768) {
      flex-direction: column;
    }

    .icon-arrow-down {
      fill: #fff;
      width: 16px;
      height: 16px;
    }
  }

  &__img-container {
    .hero__image {
      position: absolute;
      opacity: 0.5;
      z-index: -1;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.5;
        background: linear-gradient(313deg, rgba(0, 63, 255, .1), rgba(0, 15, 58, 1), rgba(0, 15, 58, 1), rgba(0, 27, 108, .1), rgba(0, 63, 255, 0)), #102250;
        //background: rgba(16, 34, 80, 1);
        border-radius: 20px;
      }

      img {
        filter: grayscale(100%);
        position: relative;
        border-radius: 20px;
      }

      @include md {
        img {
          width: 250px;
        }
      }
      @include sm {
        img {
          width: 150px;
        }
      }

      &:nth-child(1) {
        top: -7px;
        left: 1%;
        @include xs {
          right: 0;
          left: unset;
          top: -82px;
        }
      }

      &:nth-child(2) {
        bottom: 52%;
        right: 0px;
        @include lg {
          left: 0;
          bottom: 68%;
          right: unset;
        }
      }

      &:nth-child(3) {
        bottom: 29%;
        left: 8%;
        @include xs {
          right: 0;
          left: unset;
          bottom: 40%;
        }
      }

      &:nth-child(4) {
        left: 68%;
        bottom: 12%;
        @include xs {
          left: 5%;
          bottom: 30%;
        }
      }
    }
  }

  &__videos {
    max-width: 1920px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
  }

  &__video {
    max-width: 372px;
    width: 100%;
    position: absolute;
    border-radius: 20px;
    opacity: 0;
    animation: levitation 10s ease infinite;
    @include media-down(1440) {
      max-width: 320px;
    }
    @include media-down(1200) {
      max-width: 275px;
    }
    @include media-down(992) {
      max-width: 200px;
      animation: none;
      border-radius: 10px;
    }
    @include media-down(576) {
      max-width: 150px;
    }

    &:nth-child(1) {
      top: 15px;
      right: 103px;
      animation-direction: reverse;
      @include media-down(1440) {
        right: 50px;
      }
      @include media-down(1200) {
        right: 35px;
      }
      @include media-down(576) {
        top: 82px;
        right: 0;
      }
    }

    &:nth-child(2) {
      max-width: 409px;
      top: 205px;
      left: 26px;
      @include media-down(1440) {
        max-width: 340px;
        top: 160px;
        left: 10px;
      }
      @include media-down(1200) {
        max-width: 300px;
        top: 130px;
        left: 5px;
      }
      @include media-down(992) {
        max-width: 200px;
      }
      @include media-down(576) {
        max-width: 150px;
        top: 100px;
        left: -5px;
      }
    }

    &:nth-child(3) {
      top: 553px;
      right: -33px;
      @include media-down(1440) {
        right: -15px;
        top: 475px;
      }
      @include media-down(1200) {
        right: -10px;
        top: 420px;
      }
      @include media-down(576) {
        top: 294px;
        right: unset;
        left: -43px;
      }
    }

    &:nth-child(4) {
      max-width: 476px;
      top: 685px;
      left: 160px;
      animation-direction: reverse;
      @include media-down(1440) {
        max-width: 360px;
        top: 600px;
        left: 90px;
      }
      @include media-down(1200) {
        max-width: 310px;
        top: 545px;
        left: 60px;
      }
      @include media-down(992) {
        max-width: 200px;
      }
      @include media-down(576) {
        max-width: 150px;
        top: 381px;
        left: unset;
        right: -5px;
      }
    }

    &:nth-child(5) {
      bottom: 136px;
      right: 315px;
      @include media-down(1440) {
        max-width: 250px;
        bottom: 100px;
      }
      @include media-down(992) {
        max-width: 200px;
      }
      @include media-down(768) {
        bottom: 20px;
        right: 50px;
      }
      @include media-down(576) {
        max-width: 150px;
        top: 521px;
        right: unset;
        left: 32px;
      }
    }
  }
}

.sponsor {
  opacity: 1 !important;
}
